@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
h2 {
  font-family: 'Oswald', sans-serif;
}
p {
  font-family: 'Open Sans', sans-serif;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #183893;
}
/* .home:before {
  content: '';
  height: 100vh;
  background-color: red;
} */
.intro {
  margin-bottom: 2rem;
}
.home h2 {
  color: #ff6d65;
  font-size: 70px;
  text-align: center;
  line-height: 1.2;
  margin-bottom: 1rem;
}
.home p {
  color: white;
  font-size: 38px;
  text-align: center;
}
.icons {
  display: flex;
  width: 200px;
  justify-content: center;
  align-items: center;
}
.social-media {
  width: 30px;
  height: 30px;
  margin: 0.5rem;
}
.cls-1 {
  fill: white;
  transition: fill 0.2s ease-in-out;
}
.social-media:hover .cls-1 {
  fill: #ff6d65;
}



.nav-bar {
  position: absolute;
  width: 100%;
  height: 50px;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 1rem;
  transition: all 0.3s ease-in-out;
  margin-top: 1rem;
}
.nav-bar.fixed {
  position: fixed;
  background-color: #0b132b;
  margin-top: 0;
}
.nav-bar.fixed li a {
  color: white;
}
.nav-bar.fixed li a .underline {
  background-color: white;
}
.nav-bar li {
  list-style: none;
}
.nav-bar li a {
  margin-right: 6rem;
  display: inline-block;
  text-decoration: none;
  color: white;
  font-family: 'Oswald', sans-serif;
  font-size: 22px;
  font-weight: 600;
  text-transform: lowercase;
  position: relative;
  padding: 0.2em;
  overflow: hidden;
  text-transform: uppercase;
}
.nav-bar li a .underline {
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: white;
  transform: scaleX(0) translateX(-100%);
  transform-origin: left center;
  transition: transform 0.3s ease-in-out;
}
.nav-bar li a:hover .underline {
  transform: scaleX(1) translateX(0%);
}

